import { FC, useCallback } from 'react';
import { ethers } from 'ethers';
import './DApp.css';
import { sendTransaction, useWallet } from './useWallet';
import { Field } from './Field';
import { useDefaultGasFee, useDefaultNonce, useValues } from './useValues';

export interface DAppProps {}

export const DApp: FC<DAppProps> = () => {
  const {
    to,
    setTo,
    nonce,
    setNonce,
    value,
    setValue,
    providerChainId,
    setProviderChainId,
    providerName,
    setProviderName,
    providerUrl,
    setProviderUrl,
    maxFeePerGas,
    setMaxFeePerGas,
    maxPriorityFeePerGas,
    setMaxPriorityFeePerGas,
  } = useValues();

  const { wallet, setProvider } = useWallet();

  const handleUpdateProvider = useCallback(() => {
    setProvider(
      new ethers.providers.JsonRpcProvider(providerUrl, {
        chainId: Number(providerChainId),
        name: providerName,
      })
    );
  }, [providerUrl, providerChainId, providerName, setProvider]);

  const handleSendTransaction = useCallback(async () => {
    if (!value || !to) {
      alert('Please fill the required fields');
      return;
    }

    try {
      const receipt = await sendTransaction(wallet, {
        value,
        nonce,
        maxFeePerGas,
        maxPriorityFeePerGas,
      });
      if (!receipt) {
        alert('Send transaction failed.');
        return;
      }

      alert('Send transaction successfully.');
    } catch (error) {
      console.error(error);
      alert('Send transaction failed.');
    }
  }, [value, to, nonce, maxFeePerGas, maxPriorityFeePerGas, wallet]);

  useDefaultNonce(wallet, setNonce);
  useDefaultGasFee(wallet, setMaxFeePerGas, setMaxPriorityFeePerGas);

  return (
    <div className="DApp">
      <h1> DApp </h1>
      <h3>BASIC</h3>
      <Field label="to" value={to} updater={setTo} required={true} />
      <Field label="value" value={value} updater={setValue} required={true} />
      <Field label="nonce" value={nonce} updater={setNonce} />
      <h3>PROVIDER</h3>
      <Field label="url" value={providerUrl} updater={setProviderUrl} />
      <Field
        label="chain id"
        value={providerChainId}
        updater={setProviderChainId}
      />
      <Field label="name" value={providerName} updater={setProviderName} />
      <button onClick={handleUpdateProvider}>update provider</button>
      <h3>GAS FEE</h3>
      <Field
        label="maxFeePerGas"
        value={maxFeePerGas}
        updater={setMaxFeePerGas}
      />
      <Field
        label="maxPriorityFeePerGas"
        value={maxPriorityFeePerGas}
        updater={setMaxPriorityFeePerGas}
      />
      <button onClick={handleSendTransaction}>Send</button>
    </div>
  );
};
